@import url(https://fonts.googleapis.com/css2?family=Secular+One&display=swap);
*{margin:0}.app{height:100vh;display:flex;flex-direction:column;background:#131d29;color:white;overflow:scroll}.app .todo-container{margin:0 auto;font-family:'Secular One', sans-serif}.app .btn{border:none;font-size:1.2em;padding:10px;border-radius:20px;width:70px;background:#13406b;color:white;font-weight:700;cursor:pointer}.app .btn:focus{outline:none}.app .btn:hover{opacity:0.9}.app nav{display:flex;align-items:center;justify-content:space-between;background:#091016;min-height:45px;padding:10px}.app nav a{padding-right:1.5rem;color:#fff;font-family:'Source Sans Pro', sans-serif;font-weight:bold}.app nav a:hover{color:#2c5169}.app .todos-container{padding:20px 10px;width:400px}.app .todos-container .todo-form .input-item input{padding:10px;border-radius:20px;border:none;background:#2c5169;margin-right:10px;color:white;font-size:1.2em;min-width:300px;font-family:'Secular One', sans-serif}.app .todos-container .todos{padding-top:30px}.app .todos-container .todos .title{display:flex;align-items:center;justify-content:space-between}.app .todos-container .todos .todo-item{padding:5px 10px;background:#2c5169;margin:10px 0;border-radius:50px;display:flex;align-items:center;justify-content:space-between}.app .todos-container .todos .todo-item p{font-family:'Secular One', sans-serif;font-weight:bold}.app .todones-container{padding:10px;width:400px}.app .todones-container .title{display:flex;align-items:center;justify-content:space-between}.app .todones-container .todones-item{padding:5px 10px;background:#2c5169;margin:10px 0;border-radius:50px;display:flex;align-items:center;justify-content:space-between}.app .todones-container .todones-item p{text-decoration:line-through;opacity:0.5;font-family:'Secular One', sans-serif}.app .help-container{height:30vh;display:flex;justify-content:center;align-items:center}.app .page-not-found{height:80vh;display:flex;justify-content:center;align-items:center;font-size:2em}

.help-container{width:30rem;margin:3rem auto;padding:1rem;display:flex;flex-direction:column;justify-content:center;align-items:center}.help-container p{margin-top:1rem;line-height:1.3em;font-family:Arial, Helvetica, sans-serif;color:#c6c7c9}.help-container .back-btn{margin-top:2rem;width:5.5rem;padding:0.5rem 0.8rem;text-decoration:none}

